import React, { useState, useEffect, useCallback, useMemo } from 'react';
import backgroundImage from './assets/background.png';
import lotteconImage from './assets/lottecon.png';
import lotteWorldImage from './assets/lotteworld.png';
import lotteGRSImage from './assets/lottegrs.png';
import lotteShoppingImage from './assets/lotteshopping.png';
import lotteInnovateImage from './assets/lotteinnovate.png';
import lotteChemImage from './assets/lottechem.png';
import lotteHotelImage from './assets/lottehotel.png';
import lotteWellfoodImage from './assets/lottewellfood.png';
import OpenAI from 'openai';
import santaFront from './assets/santa_front.png';
import santaLeft from './assets/santa_left.png';
import santaRight from './assets/santa_right.png';
import treeBaseImage from './assets/tree_base.png';
import Snowfall from 'react-snowfall';
import lotteGiantsImage from './assets/lottegiants.png';
import lotteCinemaImage from './assets/lottecinema.png';

const TILE_SIZE = 32;
const MAP_WIDTH = 20;
const MAP_HEIGHT = 15;

// OpenAI  
const openai = new OpenAI({
  apiKey: process.env.REACT_APP_OPENAI_API_KEY, // .env  API  
  dangerouslyAllowBrowser: true //   API  
});

// PlayerSprite  
const PlayerSprite = ({ direction = 'down' }) => {
  const getPlayerImage = () => {
    switch (direction) {
      case 'left':
        return santaLeft;
      case 'right':
        return santaRight;
      default: // down, up    
        return santaFront;
    }
  };

  return (
    <img 
      src={getPlayerImage()} 
      alt=""
      className="w-16 h-16"
      style={{ 
        imageRendering: 'pixelated',
        transform: 'translateY(-8px)' //      
      }}
    />
  );
};

const LotteConSprite = () => (
  <img 
    src={lotteconImage} 
    alt="" 
    className="w-16 h-16"
    style={{ imageRendering: 'pixelated' }}
  />
);

const LotteWorldSprite = () => (
  <img 
    src={lotteWorldImage} 
    alt="" 
    className="w-24 h-24"
    style={{ 
      imageRendering: 'pixelated',
      transform: 'translateY(-24px)'
    }}
  />
);

const LotteGRSSprite = () => (
  <img 
    src={lotteGRSImage} 
    alt="" 
    className="w-20 h-20"
    style={{ 
      imageRendering: 'pixelated',
      transform: 'translateY(-16px)'
    }}
  />
);

const LotteShoppingSprite = () => (
  <img 
    src={lotteShoppingImage} 
    alt="" 
    className="w-20 h-20"
    style={{ 
      imageRendering: 'pixelated',
      transform: 'translateY(-16px)'
    }}
  />
);

const LotteInnovateSprite = () => (
  <img 
    src={lotteInnovateImage} 
    alt="" 
    className="w-15 h-20"
    style={{ 
      imageRendering: 'pixelated',
      transform: 'translateY(-16px)'
    }}
  />
);

const LotteChemSprite = () => (
  <img 
    src={lotteChemImage} 
    alt="" 
    className="w-20 h-20"
    style={{ 
      imageRendering: 'pixelated',
      transform: 'translateY(-16px)'
    }}
  />
);

const LotteHotelSprite = () => (
  <img 
    src={lotteHotelImage} 
    alt="" 
    className="w-19 h-20"
    style={{ 
      imageRendering: 'pixelated',
      transform: 'translateY(-16px)'
    }}
  />
);

const LotteWellfoodSprite = () => (
  <img 
    src={lotteWellfoodImage} 
    alt="" 
    className="w-20 h-20"
    style={{ 
      imageRendering: 'pixelated',
      transform: 'translateY(-16px)'
    }}
  />
);

const LotteGiantsSprite = () => (
  <img 
    src={lotteGiantsImage} 
    alt="" 
    className="w-22 h-20"
    style={{ 
      imageRendering: 'pixelated',
      transform: 'translateY(-16px)'
    }}
  />
);

const LotteCultureworksSprite = () => (
  <img 
    src={lotteCinemaImage} 
    alt="" 
    className="w-18 h-20"
    style={{ 
      imageRendering: 'pixelated',
      transform: 'translateY(-16px)'
    }}
  />
);

const MerchantSprite = () => (
  <svg viewBox="0 0 32 32" className="w-16 h-16">
    {/*    */}
    <rect x="12" y="8" width="8" height="16" fill="#228B22" />
    {/*  */}
    <rect x="10" y="4" width="12" height="12" fill="#F4A460" />
    {/*   */}
    <circle cx="14" cy="8" r="1.5" fill="#000000" />
    <circle cx="18" cy="8" r="1.5" fill="#000000" />
    <rect x="13" y="7" width="6" height="1" fill="#FFD700" />
    {/*   */}
    <polygon points="8,4 16,0 24,4" fill="#228B22" />
    <rect x="8" y="3" width="16" height="2" fill="#FFD700" />
    {/*   */}
    <rect x="20" y="12" width="6" height="8" fill="#8B4513" />
    <rect x="21" y="14" width="4" height="4" fill="#FFD700" />
  </svg>
);

//   
const HouseSprite = () => (
  <svg viewBox="0 0 64 64" className="w-32 h-32">
    <rect x="8" y="24" width="48" height="32" fill="#8B4513" />
    <polygon points="4,24 32,4 60,24" fill="#A52A2A" />
    {/*   */}
    <rect x="16" y="32" width="12" height="12" fill="#FFD700" />
    <rect x="36" y="32" width="12" height="12" fill="#FFD700" />
    <rect x="26" y="40" width="12" height="16" fill="#4A412A" />
    {/*   */}
    <rect x="8" y="28" width="48" height="2" fill="#FF0000" /> {/*   */}
    <circle cx="22" cy="38" r="1" fill="#FF0000" /> {/*   */}
    <circle cx="42" cy="38" r="1" fill="#FF0000" />
    <rect x="28" y="44" width="8" height="2" fill="#FFD700" /> {/*   */}
  </svg>
);

const ShopSprite = () => (
  <svg viewBox="0 0 32 32" className="w-16 h-16">
    <rect x="2" y="8" width="28" height="20" fill="#DEB887" />
    <polygon points="0,8 16,0 32,8" fill="#CD853F" />
    <rect x="4" y="12" width="8" height="8" fill="#B8860B" />
    <rect x="20" y="12" width="8" height="8" fill="#B8860B" />
    <rect x="12" y="18" width="8" height="10" fill="#8B4513" />
    <rect x="0" y="6" width="32" height="2" fill="#A0522D" />
  </svg>
);

const TreeSprite = () => (
  <svg viewBox="0 0 32 32" className="w-16 h-16">
    <rect x="14" y="24" width="4" height="8" fill="#4A3728" /> {/*   */}
    <polygon points="4,24 16,8 28,24" fill="#0B5345" /> {/*   */}
    <polygon points="6,18 16,4 26,18" fill="#0B5345" /> {/*   */}
    <polygon points="8,12 16,0 24,12" fill="#0B5345" /> {/*   */}
    {/*   */}
    <circle cx="16" cy="10" r="1.5" fill="#FF0000" />
    <circle cx="20" cy="16" r="1.5" fill="#FFD700" />
    <circle cx="12" cy="16" r="1.5" fill="#FF0000" />
    <circle cx="16" cy="22" r="1.5" fill="#FFD700" />
    <circle cx="16" cy="2" r="2" fill="#FFD700" /> {/*  */}
  </svg>
);

const SnowmanSprite = () => (
  <svg viewBox="0 0 32 32" className="w-16 h-16">
    <circle cx="16" cy="24" r="6" fill="#FFFFFF" /> {/*  */}
    <circle cx="16" cy="16" r="5" fill="#FFFFFF" /> {/*  */}
    <circle cx="16" cy="9" r="4" fill="#FFFFFF" /> {/*  */}
    {/*  */}
    <rect x="14" y="8" width="1.5" height="1.5" fill="#000000" /> {/*  */}
    <rect x="18" y="8" width="1.5" height="1.5" fill="#000000" />
    <rect x="15" y="11" width="2" height="2" fill="#FF6B00" /> {/*   */}
    <rect x="12" y="15" width="8" height="1" fill="#FF0000" /> {/*  */}
    <rect x="12" y="16" width="1" height="3" fill="#FF0000" />
  </svg>
);

const OBSTACLES = [
  //   
];

const Game = () => {
  const [playerPos, setPlayerPos] = useState({ x: 10, y: 10 });
  const [playerDirection, setPlayerDirection] = useState('down');
  const [dialogue, setDialogue] = useState("");
  const [showDialogue, setShowDialogue] = useState(false);
  const [nearbyNPC, setNearbyNPC] = useState(null);
  const [heartScore, setHeartScore] = useState(0);
  const [showHeartEffect, setShowHeartEffect] = useState(false);
  const [showChatInput, setShowChatInput] = useState(false);
  const [chatInput, setChatInput] = useState("");
  const [givenHearts, setGivenHearts] = useState(new Set());
  const [showCelebration, setShowCelebration] = useState(false);

  // NPCs    
  const NPCs = useMemo(() => [
    {
      id: 'lotte-con',
      name: '',
      x: 3,
      y: 3,
      sprite: LotteConSprite,
      context: "  .   ."
    },
    {
      id: 'lotte-world',
      name: '',
      x: 15,
      y: 3,
      sprite: LotteWorldSprite,
      context: "  .   ."
    },
    {
      id: 'lotte-grs',
      name: '',
      x: 3,
      y: 11,
      sprite: LotteGRSSprite,
      context: "  .      ."
    },
    {
      id: 'lotte-shopping',
      name: '',
      x: 15,
      y: 11,
      sprite: LotteShoppingSprite,
      context: "  .   ."
    },
    {
      id: 'lotte-innovate',
      name: '',
      x: 9,  //   
      y: 11, //   
      sprite: LotteInnovateSprite,
      context: `  . 
       , , , , ,        , , ,    IT     ICT .
      4       , , , ,                 .
         .`
    },
    {
      id: 'lotte-chem',
      name: '',
      x: 6,  //   
      y: 7,  //   
      sprite: LotteChemSprite,
      context: `  . 
            .    , ,        ,  ,    ,              .
         .`
    },
    {
      id: 'lotte-hotel',
      name: '',
      x: 14,  //   
      y: 8,   //   
      sprite: LotteHotelSprite,
      context: `  . 
                      .                   .
           .`
    },
    {
      id: 'lotte-wellfood',
      name: '',
      x: 16,  //  
      y: 6,   //   
      sprite: LotteWellfoodSprite,
      context: `  . 
         ,                   . , ,   , HMR, ,                  .
             .`
    },
    {
      id: 'lotte-giants',
      name: '',
      x: 10,  //   
      y: 3,   //   
      sprite: LotteGiantsSprite,
      context: `  . 
        ,            .
          .`
    },
    {
      id: 'lotte-cultureworks',
      name: '',
      x: 7,  //   
      y: 2,   //  
      sprite: LotteCultureworksSprite,
      context: `  . 
       1999 9,    20       .          ,        .
           .`
    }
  ], []); //   - NPC  

  // BUILDINGS    
  const BUILDINGS = useMemo(() => [
    // ...   ...
    {
      id: 'christmas-tree',
      x: 8,
      y: 6,
      width: 3,
      height: 3,
      sprite: () => (
        <ChristmasTreeSprite 
          decorationLevel={heartScore}
          totalNPCs={NPCs.length}
          showCelebration={showCelebration}
        />
      )
    },
  ], [heartScore, showCelebration, NPCs.length]);

  const isColliding = (newPos) => {
    // NPC  
    const npcCollision = NPCs.some(npc => 
      npc.x === newPos.x && npc.y === newPos.y
    );
    
    //   
    const buildingCollision = BUILDINGS.some(building => 
      newPos.x >= building.x && 
      newPos.x < building.x + building.width &&
      newPos.y >= building.y && 
      newPos.y < building.y + building.height
    );
    
    //   
    const obstacleCollision = OBSTACLES.some(obstacle => 
      obstacle.x === newPos.x && obstacle.y === newPos.y
    );

    return npcCollision || buildingCollision || obstacleCollision;
  };

  // NPC     
  const checkNPCProximity = (newPos) => {
    const nearby = NPCs.find(npc => 
      Math.abs(npc.x - newPos.x) <= 1 && 
      Math.abs(npc.y - newPos.y) <= 1
    );

    if (nearby) {
      setNearbyNPC(nearby);
      startDialogue(nearby);
    } else {
      setNearbyNPC(null);
      setShowDialogue(false);
    }
  };

  //   
  const startDialogue = async (npc) => {
    setShowDialogue(true);
    setDialogue(`${npc.name}: ...`);

    try {
      const response = await openai.chat.completions.create({
        model: "gpt-3.5-turbo",
        messages: [
          {
            role: "system",
            content: npc.context
          },
          {
            role: "user",
            content: " .    ."
          }
        ],
        max_tokens: 100,
        temperature: 0.7,
      });

      const newDialogue = response.choices[0].message.content;
      setDialogue(`${npc.name}: ${newDialogue}`);
    } catch (error) {
      console.error('ChatGPT API :', error);
      setDialogue(`${npc.name}: .     .`);
    }
  };

  // movePlayer  
  const movePlayer = useCallback((e) => {
    if (showChatInput && e.key !== 'Enter' && e.key !== 'Escape') {
      return; //     
    }

    e.preventDefault();
    let newPos = { ...playerPos };
    let newDirection = playerDirection;

    switch (e.key) {
      case 'ArrowUp':
        if (newPos.y > 0) newPos.y -= 1;
        newDirection = 'up';
        break;
      case 'ArrowDown':
        if (newPos.y < MAP_HEIGHT - 1) newPos.y += 1;
        newDirection = 'down';
        break;
      case 'ArrowLeft':
        if (newPos.x > 0) newPos.x -= 1;
        newDirection = 'left';
        break;
      case 'ArrowRight':
        if (newPos.x < MAP_WIDTH - 1) newPos.x += 1;
        newDirection = 'right';
        break;
      case 'l':
      case 'L':
        if (nearbyNPC) {
          if (givenHearts.has(nearbyNPC.id)) {
            //    NPC 
            setDialogue(`${nearbyNPC.name}:   !`);
            setShowDialogue(true);
            setTimeout(() => {
              setShowDialogue(false);
            }, 2000);
          } else {
            //    
            setGivenHearts(prev => new Set([...prev, nearbyNPC.id]));
            setHeartScore(prev => {
              const newScore = prev + 1;
              //  NPC   
              if (newScore === NPCs.length) {
                setShowCelebration(true);
                // 5    
                setTimeout(() => {
                  setShowCelebration(false);
                }, 5000);
              }
              return newScore;
            });
            setShowHeartEffect(true);
            setDialogue(`${nearbyNPC.name}:  !`);
            setShowDialogue(true);
            setTimeout(() => {
              setShowDialogue(false);
              setShowHeartEffect(false);
            }, 2000);
          }
        }
        return;
      case 'c':
      case 'C': // 'C'    (  )
        if (nearbyNPC && !showChatInput) {
          setShowChatInput(true);
          e.preventDefault();
        }
        break;
      case 'Escape': // ESC  
        if (showChatInput) {
          setShowChatInput(false);
          setChatInput("");
        }
        break;
      default:
        return;
    }

    if (!isColliding(newPos)) {
      setPlayerPos(newPos);
      checkNPCProximity(newPos);
    }
    setPlayerDirection(newDirection);
  }, [playerPos, playerDirection, nearbyNPC, showChatInput]);

  //    
  const handleChatSubmit = async (e) => {
    e.preventDefault();
    if (!chatInput.trim() || !nearbyNPC) return;

    setShowDialogue(true);
    setDialogue(`${nearbyNPC.name}: ...`);

    try {
      const response = await openai.chat.completions.create({
        model: "gpt-3.5-turbo",
        messages: [
          {
            role: "system",
            content: nearbyNPC.context
          },
          {
            role: "user",
            content: chatInput
          }
        ],
        max_tokens: 150,
        temperature: 0.7,
      });

      const newDialogue = response.choices[0].message.content;
      setDialogue(`${nearbyNPC.name}: ${newDialogue}`);
    } catch (error) {
      console.error('ChatGPT API :', error);
      setDialogue(`${nearbyNPC.name}: .     .`);
    }

    setChatInput("");
    setShowChatInput(false);
  };

  useEffect(() => {
    window.addEventListener('keydown', movePlayer);
    return () => window.removeEventListener('keydown', movePlayer);
  }, [movePlayer]); // movePlayer   

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gradient-to-b from-blue-900 to-blue-700 p-4">
      {/*       */}
      <div className="mb-4 flex flex-col items-center">
        <div className="text-2xl font-bold text-white mb-2">
            
        </div>
        {/*     */}
        <div className="bg-white px-4 py-2 rounded-lg shadow-lg flex flex-col items-center gap-1">
          <div className="flex items-center gap-2">
            <span className="font-bold text-gray-700"> </span>
            <div className="flex items-center bg-pink-50 px-3 py-1 rounded-full border-2 border-pink-300">
              <span className="mr-2 text-xl" style={{ filter: 'drop-shadow(0 0 2px rgba(0,0,0,0.3))' }}>
                
              </span>
              <span className="font-bold text-pink-600 text-xl">
                {heartScore} / {NPCs.length} 
              </span>
            </div>
          </div>
          <div className="text-sm text-gray-600 italic">
              L     !
               !
          </div>
        </div>
      </div>

      <div className="relative">
        {/*     */}
        {showCelebration && (
          <div className="absolute inset-0 overflow-hidden" style={{ 
            width: MAP_WIDTH * TILE_SIZE, 
            height: MAP_HEIGHT * TILE_SIZE,
            pointerEvents: 'none',
            zIndex: 50
          }}>
            {Array.from({ length: 50 }).map((_, i) => (
              <div
                key={`map-firework-${i}`}
                className="absolute"
                style={{
                  left: `${Math.random() * 100}%`,
                  top: `${Math.random() * 100}%`,
                  fontSize: '24px',
                  animation: `firework ${1 + Math.random()}s ease-out infinite`,
                  animationDelay: `${Math.random() * 2}s`
                }}
              >
                {['', '', '', '', ''][Math.floor(Math.random() * 5)]}
              </div>
            ))}
          </div>
        )}
        
        {/*    */}
        <div className="relative rounded-lg overflow-hidden shadow-lg" 
          style={{ 
            width: MAP_WIDTH * TILE_SIZE, 
            height: MAP_HEIGHT * TILE_SIZE,
            backgroundImage: `url(${backgroundImage})`,
            backgroundSize: 'cover',
            backgroundPosition: 'center'
          }}>
          <Snowfall />
          {/*  */}
          {BUILDINGS.map(building => (
            <div 
              key={building.id}
              className="absolute"
              style={{ 
                left: building.x * TILE_SIZE, 
                top: building.y * TILE_SIZE,
                width: building.width * TILE_SIZE,
                height: building.height * TILE_SIZE
              }}
            >
              <building.sprite />
            </div>
          ))}

          {/*  */}
          {OBSTACLES.map(obstacle => (
            <div 
              key={obstacle.id}
              className="absolute"
              style={{ 
                left: obstacle.x * TILE_SIZE, 
                top: obstacle.y * TILE_SIZE 
              }}
            >
              <obstacle.sprite />
            </div>
          ))}

          {/* NPCs */}
          {NPCs.map(npc => (
            <div 
              key={npc.id}
              className="absolute"
              style={{ 
                left: npc.x * TILE_SIZE, 
                top: npc.y * TILE_SIZE 
              }}
            >
              <npc.sprite />
            </div>
          ))}
          
          {/*    */}
          <div 
            className="absolute transition-all duration-100"
            style={{ 
              left: playerPos.x * TILE_SIZE, 
              top: playerPos.y * TILE_SIZE 
            }}
          >
            {/*   PlayerSprite   */}
            {showHeartEffect && (
              <div className="absolute left-1/2 -translate-x-1/2 -top-8 animate-float-heart">
                
              </div>
            )}
            <PlayerSprite direction={playerDirection} />
          </div>
        </div>
      </div>

      {/*  */}
      {showDialogue && (
        <div className="mt-4 p-3 bg-white rounded-lg shadow-lg w-80 border-2 border-gray-300">
          {dialogue}
        </div>
      )}

      {/*   */}
      {showChatInput && nearbyNPC && (
        <form onSubmit={handleChatSubmit} className="mt-4 w-80">
          <div className="flex gap-2">
            <input
              type="text"
              value={chatInput}
              onChange={(e) => setChatInput(e.target.value)}
              onKeyDown={(e) => {
                if (e.key === 'Enter') {
                  e.preventDefault();
                  handleChatSubmit(e);
                }
              }}
              className="flex-1 p-2 rounded-lg border-2 border-gray-300 focus:outline-none focus:border-blue-500"
              placeholder={`${nearbyNPC.name}  ... (Enter: , ESC: )`}
              autoFocus
            />
            <button
              type="submit"
              className="px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors"
            >
              
            </button>
          </div>
        </form>
      )}

      <div className="mt-4 text-xl font-semibold text-white bg-blue-800 px-4 py-2 rounded-lg shadow-lg">
         , C , L  
      </div>
    </div>
  );
};

// ChristmasTreeSprite  Game   
const ChristmasTreeSprite = ({ 
  decorationLevel = 0, 
  totalNPCs = 0,
  showCelebration = false 
}) => {
  const isComplete = decorationLevel === totalNPCs;
  
  const decorations = [
    { top: '20%', left: '30%', emoji: '', color: '#FFD700' },
    { top: '35%', left: '60%', emoji: '', color: '#FF0000' },
    { top: '50%', left: '25%', emoji: '', color: '#FF69B4' },
    { top: '65%', left: '55%', emoji: '', color: '#FFD700' },
    { top: '40%', left: '40%', emoji: '', color: '#FFFFFF' }
  ];

  const celebrationEmojis = ['', '', '', '', ''];

  return (
    <div className="relative">
      <img 
        src={treeBaseImage} 
        alt=" " 
        className={`w-48 h-48 ${isComplete ? 'celebration-tree' : ''}`}
        style={{ 
          imageRendering: 'pixelated',
          filter: `drop-shadow(0 0 15px rgba(0, 255, 0, 0.3)) 
                  ${isComplete ? 'brightness(1.3)' : ''}`,
          transition: 'all 0.3s ease-in-out'
        }}
      />
      {decorations.slice(0, decorationLevel).map((decoration, index) => (
        <div
          key={index}
          className="absolute animate-twinkle"
          style={{
            top: decoration.top,
            left: decoration.left,
            fontSize: '20px',
            textShadow: `0 0 5px ${decoration.color}`,
            animation: 'twinkle 2s ease-in-out infinite'
          }}
        >
          {decoration.emoji}
        </div>
      ))}
      
      {/*   */}
      {isComplete && showCelebration && (
        <>
          {/*   */}
          {Array.from({ length: 20 }).map((_, i) => (
            <div
              key={`firework-${i}`}
              className="absolute"
              style={{
                left: `${Math.random() * 100}%`,
                animation: `firework ${1 + Math.random()}s ease-out infinite`,
                animationDelay: `${Math.random() * 2}s`
              }}
            >
              {celebrationEmojis[Math.floor(Math.random() * celebrationEmojis.length)]}
            </div>
          ))}
          
          {/*   */}
          {Array.from({ length: 30 }).map((_, i) => (
            <div
              key={`confetti-${i}`}
              className="absolute"
              style={{
                left: `${Math.random() * 100}%`,
                top: `-${Math.random() * 100}%`,
                color: `hsl(${Math.random() * 360}, 100%, 50%)`,
                animation: `confetti ${2 + Math.random() * 3}s linear infinite`,
                animationDelay: `${Math.random() * 2}s`
              }}
            >
              
            </div>
          ))}
        </>
      )}
    </div>
  );
};

// tailwind.css style   
const style = document.createElement('style');
style.textContent = `
  @keyframes float-heart {
    0% {
      transform: translateY(0) scale(0.8);
      opacity: 1;
    }
    50% {
      transform: translateY(-30px) scale(2.5);
      opacity: 0.8;
    }
    100% {
      transform: translateY(-50px) scale(3);
      opacity: 0;
    }
  }
  .animate-float-heart {
    animation: float-heart 1.5s ease-out forwards;
    font-size: 32px;  //   24px 32px 
    pointer-events: none;
    text-shadow: 0 0 10px rgba(255, 192, 203, 0.8);  //    
    filter: drop-shadow(0 0 5px rgba(255, 0, 0, 0.6));  //    
  }
  @keyframes twinkle {
    0%, 100% { opacity: 1; transform: scale(1); }
    50% { opacity: 0.7; transform: scale(1.2); }
  }

  @keyframes celebrate {
    0%, 100% { 
      transform: scale(1) translateY(-16px); 
      filter: brightness(1); 
    }
    50% { 
      transform: scale(1.3) translateY(-16px); 
      filter: brightness(1.5) drop-shadow(0 0 30px gold); 
    }
  }

  @keyframes firework {
    0% { 
      transform: translateY(0) scale(1); 
      opacity: 1; 
    }
    50% { 
      transform: translateY(-150px) scale(1.5); 
      opacity: 0.8; 
    }
    100% { 
      transform: translateY(-300px) scale(3); 
      opacity: 0; 
    }
  }

  @keyframes confetti {
    0% { transform: translateY(0) rotate(0deg); opacity: 1; }
    100% { transform: translateY(100vh) rotate(720deg); opacity: 0; }
  }

  .celebration-tree {
    animation: celebrate 1.5s infinite;
    filter: drop-shadow(0 0 30px gold);
  }
`;
document.head.appendChild(style);

export default Game; 